.btn {
	display: inline-block;
	box-sizing: border-box;
	cursor: pointer;
	text-align: center;
	vertical-align: top;
	text-decoration: none;
	background: #1a237e;
	color: #ffffff;
	padding: 12px 35px 15px;
	border-radius: 5px;
	border: 1px solid #1a237e;
	transition: all 0.3s ease;
}

