	z-index: 7;
	background: rgba(255, 255, 255, 0);
	border: 1px solid #ffffff;
}

.btn--white:hover {
	background: white;
	color: #10154b;
	box-shadow: none;
}

.btn--spare {
	display: block;
	width: 400px;
	margin: 0 auto;
}

