	box-sizing: border-box;
	width: 100%;
	outline: none;
	font-family: "Roboto", Arial, sans-serif;
	font-size: 18px;
	line-height: 20px;
}

label {
	cursor: pointer;
}

button {
	outline: none;
	cursor: pointer;
