.overlay {
	background-color: black; /*   */
	opacity: 0; /*   */
	position: fixed; /*   */
	z-index: 9980;
	right: 0; /*  0 */
	top: 0; /*  0 */
	left: 0; /*  0 */
	bottom: 0; /*  0 */
	visibility: hidden; /*   */ /* transition  */
	transition: 0.5s ease;
}

.overlay:target {
	visibility: visible; /*   */
	opacity: 0.5; /*     */
}

.popup {
	position: fixed; /*   */
	visibility: hidden; /*  -  */
	z-index: 9999; /* z-index */
	transform: translateX(-50%);
	left: 50%;
	top: 30px;
	width: 500px;
	text-align: center;
	box-sizing: border-box;
	padding: 40px 40px;
	background-color: white; /*  */
	color: #050719; /*  */
	font-size: 17px;
	font-weight: 300;
	border-top: 3px solid #1a237e;
	border-bottom: 8px solid #1a237e;
}

.overlay:target + .popup {
	opacity: 1; /*   */
	visibility: visible; /*   */
}

.popup-close {
	display: block;
	width: 3px;
	height: 20px;
	background: #1a237e;
	position: absolute;
	top: 7px;
	right: 15px;
	font-size: 0px;
	transform: rotate(45deg);
}

.popup-close:after {
	content: "";
	display: block;
	transform: rotate(-90deg);
	width: 3px;
	height: 20px;
	background: #1a237e;
	position: absolute;
	top: 0;
}

.popup__description {
	font-size: 20px;
	font-weight: 400;
}

.popup__form {
	margin-top: 20px;
}

.popup__form-line {
	margin-top: 20px;
}

.popup__form-line label {
	display: block;
	margin-bottom: 7px;
	text-align: left;
}

.popup__form-line input,
.popup__form-line textarea {
	display: block;
	border: none;
	outline: none;
	border: 2px solid #e1dddd;
	padding: 10px;
	height: 50px;
	font-size: 17px;
	border-radius: 3px;
}

.popup__form-line input:hover,
.popup__form-line input:hover:focus,
.popup__form-line input:focus,
.popup__form-line textarea:hover,
.popup__form-line textarea:hover:focus,
.popup__form-line textarea:focus {
	border: 2px solid #1a237e;
	box-shadow: 0 1px 3px 3px #d8daf0;
}

.popup__form-line textarea {
	height: 100px;
	resize: none;
}

.popup__footer {
	margin-top: 20px;
}

.popup__footer span {
	display: block;
	font-weight: 400;
	font-size: 20px;
}

