	left: 0;
	right: 0; /* Additional styles */ /* Translate -100% to move off screen */
	-webkit-transform: translateY(-100%);
	-ms-transform: translateY(-100%);
	transform: translateY(-100%); /* Animations */
	-webkit-transition: all 300ms ease-in-out;
